import { memo, useLayoutEffect, useRef, useState } from "react";

function SectionRunLine({ className }) {
    const [lengthClone, setLengthClone] = useState(0);
    const line = useRef();
    const item = useRef();
    useLayoutEffect(() => {
        const widthItem = item.current.offsetWidth;
        const widthLine = line.current.offsetWidth;

        setLengthClone(Math.ceil(widthLine / widthItem) + 1);
    }, [])

    let renderContent = [
        <div key={-1} className="section-run-line__line__item" ref={item}>доступна оплата долями по карте сбера</div>
    ]

    renderContent = renderContent.concat(Array.from(Array(lengthClone), (_, index) => {
        return <div key={index} className="section-run-line__line__item">доступна оплата долями по карте сбера</div>
    }))

    return (<>
        <section className={["section", "section-run-line", className].join(' ')}>
            <div className="section-run-line__line" ref={line}>
                <div className="section-run-line__line__wrapper" style={{
                    animationDuration: (renderContent.length * 5) + 's',
                }}>
                    {renderContent}
                </div>
                <div className="section-run-line__line__wrapper" style={{
                    animationDuration: (renderContent.length * 5) + 's',
                }}>
                    {renderContent}
                </div>
                <div className="section-run-line__line__wrapper" style={{
                    animationDuration: (renderContent.length * 5) + 's',
                }}>
                    {renderContent}
                </div>
            </div>
        </section>
    </>)
}

export default memo(SectionRunLine);