import { memo } from "react"
import { Link, NavLink, useParams } from "react-router-dom";

import { ROUTES } from "../utils/routes";
import { convertNumToPrice, createSlug } from "../utils/tools";
import { useGetCategoresQuery, useGetProductsQuery } from "../features/api/storeShop";

function SectionCatalogList(props) {
    const { slugCategory } = useParams()
    const {data: products, isSuccess: isSuccessProducts} = useGetProductsQuery()
    const {data: categories, isSuccess: isSuccessCategories} = useGetCategoresQuery();

    let renderCategories;
    if(isSuccessCategories) {
        renderCategories = categories
        .filter((_, index) => index < 6) // Test: 0
        .map(data => {
            // const isActiveCategory = activeCategory !== undefined && createSlug(data.category.name) === activeCategory;
            return (
                <NavLink to={ROUTES.CATALOG +'/' + createSlug(data.name)} 
                    key={data.id} className="section-catalog-list__category__item"
                >{data.name}</NavLink>
            )
        })
    } else {
        renderCategories = Array.from(Array(5), (_, index) => {
            return ( <NavLink key={index} className="section-catalog-list__category__item section-catalog-list__category__item--preloader"></NavLink> )
        })
    }

    let renderProducts;
    if(isSuccessProducts) {
        renderProducts = products
        .filter((_, index) => index < 20) // Test: 0
        .filter(data => {
            if(slugCategory === undefined) return true;
            return createSlug(data.category.name) === slugCategory;
        })
        .map((data, index) => {
            const classes = ["section-catalog-list__list__item"];
            if(index === 0) classes.push('section-catalog-list__list__item--sale');
            return (
                <Link key={data.id} to={ROUTES.PRODUCT + '/' + createSlug(data.title)} className={classes.join(' ')}>
                    <div className="section-catalog-list__list__img">
                        <img src={data.images[0]} alt="" />
                    </div>
                    <div className="section-catalog-list__list__name">{data.title}</div>
                    <div className="section-catalog-list__list__price">
                        {index === 0 ? <div className="section-catalog-list__list__price__old">{convertNumToPrice(data.price * 150)} Р</div> : null }
                        <div className="section-catalog-list__list__price__current">{convertNumToPrice(data.price * 100)} Р</div>
                    </div>
                </Link>
            )
        })

        if(renderProducts.length === 0) {
            renderProducts = <div className="section-catalog-list__list__notice">Извините, но в этой категории еще нет товаров</div>
        } 
    } else {
        renderProducts = Array.from(Array(6), (_, index) => {
            return (
            <Link key={index} className="section-catalog-list__list__item section-catalog-list__list__item--preloader">
                <div className="section-catalog-list__list__img"></div>
                <div className="section-catalog-list__list__name"></div>
                <div className="section-catalog-list__list__price">
                    <div className="section-catalog-list__list__price__current"></div>
                </div>
            </Link>
            )
        } )
    }

    return (
        <section className="section section-padding section-catalog-list">
            <h2 className="section__title container">Катало́г</h2>
            <div className="section-catalog-list__category container">
                <div className="section-catalog-list__category__wrapper">
                    {renderCategories}
                </div>
            </div>
            <div className="section-catalog-list__list container">
                <div className="section-catalog-list__list__wrapper">
                    {renderProducts}
                </div>
            </div>
        </section>
    )
}

export default memo(SectionCatalogList);