import { NavLink } from "react-router-dom"

import { ROUTES } from "../../utils/routes"

import Basket from "../Basket/Basket"

export default function Header() {
    return (
        <div className="header">
            <nav className="header__container container">
                <NavLink to={ROUTES.HOME} className="header__logo">
                    <img src={process.env.REACT_APP_PUBLIC_URL + '/logos/logo-black.svg'} alt="Logo woowto" />
                </NavLink>
                <ul className="header__links">
                    <li className="header__link"><NavLink to={ROUTES.CATALOG}>Каталог</NavLink></li>
                    <li className="header__link"><NavLink to={ROUTES.SALES}>Sales</NavLink></li>
                    <li className="header__link"><NavLink to={ROUTES.ABOUT}>О нас</NavLink></li>
                    <li className="header__link"><NavLink to={ROUTES.DELIVERY}>Доставка</NavLink></li>
                </ul>
                <Basket className="header__basket"/>
            </nav>
        </div>
    )
}