import { memo } from 'react';

function SectionAbout({ className }) {
    return (
        <section className={["section", "section-padding", "section-about", className].join(' ')}>
            <div className="container">
                <div className="section-about__col-left">
                    <h2 className='section__title'>О на́с</h2>
                    <p>Разнообразный и богатый опыт консультация с широким активом требуют определения и уточнения модели развития. <strong>Идейные соображения высшего порядка,</strong> а также рамки и место обучения кадров требуют определения и уточнения систем массового участия.</p>
                    <p>Не следует, однако забывать, что сложившаяся структура организации обеспечивает широкому кругу (специалистов) участие в формировании системы обучения кадров, соответствует насущным потребностям. <strong>Идейные соображения высшего порядка, а также дальнейшее развитие различных форм деятельности в значительной степени</strong> обуславливает создание форм развития.</p>
                </div>
                <div className="section-about__col-right">
                    <img src={process.env.REACT_APP_PUBLIC_URL + "/section-about-img.png"} alt="" />
                </div>
            </div>
        </section>
    )
}

export default memo(SectionAbout);