import { memo } from 'react';
import { Link } from 'react-router-dom';
import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext, DotGroup } from 'pure-react-carousel';

import { ROUTES } from '../utils/routes';
import { useGetCategoresQuery } from '../features/api/storeShop';
import { createSlug } from '../utils/tools';

function SectionCatalog({ className }) {
    const { data: categories, isSuccess } = useGetCategoresQuery();
    const iconLink = <div className="section-catalog__carousel-icon">
        <svg width="96" height="96" viewBox="0 0 96 98" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="48" cy="48" r="48" fill="#5C5681"/>
            <path d="M67.025 29.9313C66.6991 29.1297 66.0754 28.4928 65.2905 28.1599C64.9043 27.9918 64.4896 27.9026 64.0699 27.8974H31.9486C31.0967 27.8974 30.2797 28.2431 29.6773 28.8583C29.0749 29.4735 28.7365 30.3079 28.7365 31.1779C28.7365 32.0479 29.0749 32.8823 29.6773 33.4975C30.2797 34.1127 31.0967 34.4584 31.9486 34.4584H56.3287L29.668 61.6534C29.367 61.9584 29.128 62.3212 28.9649 62.721C28.8019 63.1207 28.7179 63.5495 28.7179 63.9826C28.7179 64.4156 28.8019 64.8444 28.9649 65.2442C29.128 65.6439 29.367 66.0067 29.668 66.3117C29.9666 66.6192 30.3219 66.8632 30.7133 67.0298C31.1048 67.1963 31.5246 67.282 31.9486 67.282C32.3727 67.282 32.7925 67.1963 33.184 67.0298C33.5754 66.8632 33.9306 66.6192 34.2293 66.3117L60.8578 39.0838V63.9826C60.8578 64.8526 61.1962 65.687 61.7986 66.3022C62.4009 66.9174 63.218 67.263 64.0699 67.263C64.9218 67.263 65.7388 66.9174 66.3412 66.3022C66.9436 65.687 67.282 64.8526 67.282 63.9826V31.1779C67.2769 30.7492 67.1896 30.3257 67.025 29.9313Z" fill="#F8F8F8"/>
        </svg>
    </div>

    let slides;
    if(isSuccess) {
        slides = categories
        .filter((_, index) => index < 6) // Test: 0
        .map((data, index) => {
            return (<Slide key={data.id} index={index}><Link to={ROUTES.CATALOG + '/' + createSlug(data.name)}>
                <div className='section-catalog__carousel-title'>{data.name}</div>
                <img className='section-catalog__carousel-image' src={data.image} alt={data.alt} loading='lazy'/>
                {iconLink}
            </Link></Slide>)
        });
    } else {
        slides = [
            <Slide key={0} index={0}>{iconLink}</Slide>,
            <Slide key={1} index={1}>{iconLink}</Slide>,
            <Slide key={2} index={2}>{iconLink}</Slide>,
        ]
    }

    return (
        <section className={["section", "section-padding", "section-catalog", className].join(' ')}>
            <h2 className="container section__title">Катало́г</h2>
            <CarouselProvider 
                className="container"
                naturalSlideHeight={557}
                naturalSlideWidth={380}
                totalSlides={slides.length}
                visibleSlides={3}
            >
                <Slider>
                    {slides}
                </Slider>
                <div className='carousel__controls'>
                    <ButtonBack>
                        <svg width="19" height="20" viewBox="0 0 19 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M0.128183 1.01609C0.288546 0.615634 0.595407 0.29741 0.981556 0.131108C1.17155 0.0471315 1.37556 0.00258936 1.58208 0H17.3853C17.8044 0 18.2063 0.172664 18.5027 0.480008C18.7991 0.787351 18.9656 1.2042 18.9656 1.63885C18.9656 2.0735 18.7991 2.49034 18.5027 2.79769C18.2063 3.10503 17.8044 3.2777 17.3853 3.2777H5.39064L18.5073 16.8637C18.6554 17.0161 18.773 17.1974 18.8532 17.3971C18.9334 17.5968 18.9747 17.811 18.9747 18.0273C18.9747 18.2437 18.9334 18.4579 18.8532 18.6576C18.773 18.8573 18.6554 19.0386 18.5073 19.1909C18.3604 19.3445 18.1856 19.4664 17.993 19.5496C17.8004 19.6328 17.5939 19.6757 17.3853 19.6757C17.1766 19.6757 16.9701 19.6328 16.7775 19.5496C16.5849 19.4664 16.4101 19.3445 16.2632 19.1909L3.16239 5.58847V18.0273C3.16239 18.462 2.9959 18.8788 2.69953 19.1862C2.40316 19.4935 2.0012 19.6662 1.58208 19.6662C1.16295 19.6662 0.760988 19.4935 0.464621 19.1862C0.168255 18.8788 0.00175858 18.462 0.00175858 18.0273V1.63885C0.00425529 1.42469 0.0472069 1.21311 0.128183 1.01609Z" fill="#0D0D0D"/>
                        </svg>
                    </ButtonBack>
                    <DotGroup showAsSelectedForCurrentSlideOnly={true}/>
                    <ButtonNext>
                        <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M19.1979 1.01609C19.0375 0.615634 18.7306 0.29741 18.3445 0.131108C18.1545 0.0471315 17.9505 0.00258936 17.744 0H1.9408C1.52167 0 1.11971 0.172664 0.823346 0.480008C0.526979 0.787351 0.360482 1.2042 0.360482 1.63885C0.360482 2.0735 0.526979 2.49034 0.823346 2.79769C1.11971 3.10503 1.52167 3.2777 1.9408 3.2777H13.9354L0.818774 16.8637C0.670653 17.0161 0.553087 17.1974 0.472856 17.3971C0.392625 17.5968 0.351318 17.811 0.351318 18.0273C0.351318 18.2437 0.392625 18.4579 0.472856 18.6576C0.553087 18.8573 0.670653 19.0386 0.818774 19.1909C0.965685 19.3445 1.14047 19.4664 1.33305 19.5496C1.52562 19.6328 1.73218 19.6757 1.9408 19.6757C2.14942 19.6757 2.35598 19.6328 2.54855 19.5496C2.74113 19.4664 2.91591 19.3445 3.06282 19.1909L16.1637 5.58847V18.0273C16.1637 18.462 16.3302 18.8788 16.6265 19.1862C16.9229 19.4935 17.3248 19.6662 17.744 19.6662C18.1631 19.6662 18.5651 19.4935 18.8614 19.1862C19.1578 18.8788 19.3243 18.462 19.3243 18.0273V1.63885C19.3218 1.42469 19.2788 1.21311 19.1979 1.01609Z" fill="#0D0D0D"/>
                        </svg>
                    </ButtonNext>
                </div>
            </CarouselProvider>
        </section>
    )
}

export default memo(SectionCatalog);