import { configureStore } from "@reduxjs/toolkit";

import { apiShop } from "./api/storeShop";
import currentOrderReducer from "./slice/currentOrder";

export default configureStore({
    reducer: {
        currentOrder: currentOrderReducer,
        [apiShop.reducerPath]: apiShop.reducer,
    },
    middleware: getDefaultMiddleware => getDefaultMiddleware().concat(apiShop.middleware),
    devTools: true,
})