import { Route, Routes } from "react-router-dom";

import { ROUTES as ROUTES_LINKS } from "../../utils/routes";

import Home from '../../pages/Home';
import About from '../../pages/About';
import Sales from "../../pages/Sales";
import Catalog from "../../pages/Catalog";
import Delivery from "../../pages/Delivery";
import Privacy from "../../pages/Privacy";
import OfferDoc from "../../pages/OfferDoc";
import Sertificats from "../../pages/Sertificats";
import Care from '../../pages/Care';
import Product from "../../pages/Product";

export default function AppRoutes() {
    return (
        <Routes>
            <Route index element={<Home />}/>
            <Route path={ROUTES_LINKS.CATALOG + '/' + ROUTES_LINKS.CATALOG_ACTIVE} element={<Catalog />}/>
            <Route path={ROUTES_LINKS.PRODUCT + '/' + ROUTES_LINKS.PRODUCT_ACTIVE} element={<Product />} />
            <Route path={ROUTES_LINKS.ABOUT} element={<About />}/>
            <Route path={ROUTES_LINKS.SALES} element={<Sales />}/>
            <Route path={ROUTES_LINKS.DELIVERY} element={<Delivery />}/>
            <Route path={ROUTES_LINKS.CARE} element={<Care />} />
            <Route path={ROUTES_LINKS.SERTIFICATS} element={<Sertificats />} />
            <Route path={ROUTES_LINKS.OFFERDOC} element={<OfferDoc />} />
            <Route path={ROUTES_LINKS.PRIVACY} element={<Privacy />} />
        </Routes>
    )
}