import { memo, useRef, useState } from "react";


function ToggleHeader({ title, desc }) {
    const [isOpen, setIsOpen] = useState(false);
    const text = useRef();

    const onClickHandler = () => {
        setIsOpen(prevState => !prevState);
    } 

    return (
        <div className={["section-product-info__desc__item", isOpen ? 'section-product-info__desc__item--active' : ''].join(' ')} 
            onClick={onClickHandler}
        >
            <div className="section-product-info__desc__title">{title}</div>
            <div className="section-product-info__desc__text" ref={text} style={{
                height: isOpen ? text.current.childNodes[0].offsetHeight + 'px' : 0
            }}><p>{desc}</p></div>
            <svg width="17" height="17" viewBox="0 0 17 17" fill="none" xmlns="http://www.w3.org/2000/svg">
                <circle cx="8.5" cy="8.5" r="8.5" transform="matrix(1 0 0 -1 0 17)" fill="#5C5681"/>
                <path d="M8.67149 13.1658C8.53031 13.2254 8.37244 13.227 8.23248 13.1704C8.16308 13.1431 8.09999 13.1024 8.04678 13.0504L4.02466 9.02832C3.91799 8.92165 3.85896 8.77607 3.86057 8.6236C3.86217 8.47114 3.92428 8.32428 4.03322 8.21534C4.14216 8.1064 4.28902 8.04429 4.44148 8.04269C4.59395 8.04108 4.73953 8.10011 4.8462 8.20678L7.89899 11.2596L7.96591 4.51593C7.9664 4.44005 7.98191 4.3647 8.01154 4.29422C8.04118 4.22374 8.08436 4.15954 8.13858 4.10531C8.19281 4.05109 8.25701 4.00791 8.32749 3.97827C8.39796 3.94864 8.47332 3.93313 8.5492 3.93264C8.62509 3.93153 8.70014 3.94546 8.77001 3.97362C8.83987 4.00178 8.90318 4.04361 8.95628 4.09671C9.00937 4.1498 9.05121 4.21311 9.07937 4.28298C9.10753 4.35285 9.12145 4.42789 9.12034 4.50378L9.04529 11.2475L12.163 8.12977C12.272 8.02082 12.4188 7.95872 12.5713 7.95711C12.7238 7.95551 12.8693 8.01453 12.976 8.12121C13.0827 8.22788 13.1417 8.37346 13.1401 8.52593C13.1385 8.67839 13.0764 8.82525 12.9675 8.93419L8.85976 13.0419C8.80545 13.0949 8.74149 13.137 8.67149 13.1658Z" fill="#F8F8F8"/>
            </svg>
        </div>
    )
}

export default memo(ToggleHeader);