import { memo, useEffect, useState } from "react"

function ScrollToTop() {
    const [isShow, setIsShow] = useState();
    const handlerOnClick = () => {
        if(window) {
            window.scrollTo({
                top: 0,
                behavior: "smooth",
            })
        }
    }

    useEffect(() => {
        if(window) {
            const handler = () => {
                if(window.scrollY > 200) {
                    setIsShow(true);
                }  else {
                    setIsShow(false);
                }
            }

            document.addEventListener('scroll', handler);
            return () => document.removeEventListener('scroll', handler);
        }
    }, []);


    return (
        <button onClick={handlerOnClick} className={"button-scroll-to-top " + (isShow && 'button-scroll-to-top--show')}>
            <svg width="96" height="96" viewBox="0 0 96 96" fill="none" xmlns="http://www.w3.org/2000/svg">
                <circle cx="48" cy="48" r="48" fill="#5C5681"/>
                <path d="M48.9662 21.6506C48.1689 21.3143 47.2775 21.3049 46.4871 21.6245C46.0952 21.7787 45.7389 22.0089 45.4384 22.3021L22.7253 45.0152C22.1229 45.6176 21.7896 46.4397 21.7986 47.3007C21.8077 48.1616 22.1584 48.9909 22.7736 49.6062C23.3888 50.2214 24.2181 50.5721 25.0791 50.5811C25.9401 50.5902 26.7622 50.2569 27.3646 49.6545L44.6038 32.4152L44.9817 70.4969C44.9845 70.9255 45.0721 71.351 45.2394 71.749C45.4068 72.1469 45.6506 72.5095 45.9568 72.8157C46.2631 73.1219 46.6256 73.3658 47.0236 73.5331C47.4216 73.7005 47.8471 73.7881 48.2756 73.7908C48.7042 73.7971 49.128 73.7184 49.5225 73.5594C49.9171 73.4004 50.2746 73.1642 50.5744 72.8643C50.8742 72.5645 51.1105 72.207 51.2695 71.8124C51.4285 71.4179 51.5072 70.9941 51.5009 70.5655L51.0771 32.4833L68.6831 50.0894C69.2984 50.7046 70.1277 51.0553 70.9886 51.0644C71.8496 51.0735 72.6717 50.7401 73.2741 50.1377C73.8765 49.5353 74.2098 48.7132 74.2008 47.8523C74.1917 46.9913 73.841 46.162 73.2258 45.5468L50.0294 22.3504C49.7227 22.0508 49.3615 21.8131 48.9662 21.6506Z" fill="#F8F8F8"/>
            </svg>
        </button>
    )   
}

export default memo(ScrollToTop)