import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

export const apiShop = createApi({
    reducerPath: 'shop',
    baseQuery: fetchBaseQuery({ baseUrl: 'https://api.escuelajs.co/api/v1'}),
    endpoints: builder => ({
        getProducts: builder.query({
            query: () => '/products' 
        }),
        getCategores: builder.query({
            query: () => '/categories'
        }),
        getStories: builder.query({
            query: () => '/products',
        })
    }),
})

export const { useGetProductsQuery, useGetCategoresQuery, useGetStoriesQuery } = apiShop;